<template>
  <transition name="top-in">
    <el-aside :width="widen? '30vw' : '20vw'" :class="['aside-layout', widen?'is-widened': 'not-widened']" v-if="isShow">
    </el-aside>
  </transition>
</template>

<script>
export default {
  name: "PageNav",
  props: {
    isShow: {
      type: Boolean,
      default() {
        return false
      }
    },
    widen: {
      type: Boolean,
      required: true,
    }
  }
};
</script>

<style scoped lang="scss">
.aside-layout {
  z-index: 1;
  background: linear-gradient(45deg, #75BAD2, #8ADBF8);
}

.top-in-enter-active {
  animation: top-in .8s;
}
.top-in-leave-active {
  animation: top-in .8s reverse;
}
@keyframes top-in {
  from {
    transform: translateY(-100%);
  }
  to {
    transform: translateY(0);
  }
}
.is-widened {
  animation: widen 1s ease;
}
.not-widened {
  animation: nowiden 1s ease;
}
@keyframes widen {
  from {
    width: 20vw;
  }
  to {
    width: 30vw;
  }
}
@keyframes nowiden {
  from {
    width: 30vw;
  }
  to {
    width: 20vw;
  }
}
</style>
